body { 
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 20px;
  /* background-color: red; */
  
}

.header {
  overflow: hidden;
  background-color: rgba(255, 0, 0, 0.61);
  padding: 20px 10px;
  height: 30px;
}

.header a {
  float: left;
  color: white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px; 
  line-height: 25px;
  border-radius: 4px;
}

.header a.logo {
  font-size: 25px;
  font-weight: bold;
}

.header a:hover {
  background-color:rgba(255, 0, 0, 0.863);
  color: white;
}

.header-right {
  float: right;
}

@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  
  .header-right {
    float: none;
  }
}